.header {
  margin: 0 auto;
  max-width: 1110px;
  min-height: 77px;
  display: flex;
  align-items: center;
  position: sticky;
  top: 0;
  justify-content: space-between;
  z-index: 99;
  background-color: var(--main-bg-color);
  box-shadow: 0px 1px 10px #f8f8f8;
  border-radius: 0px 0px 10px 10px;
}

.header__logo-container {
  display: flex;
  gap: 5.44px;
  align-items: center;
}

.header__button-container {
  display: flex;
  gap: 25px;
}

.header__nav {
  display: flex;
  gap: 15px;
}

.header__nav-link {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  color: var(--color-text-fifth);
  cursor: pointer;
  transition: opasity 0.3s ease-in-out;
}

.header__nav-link:hover {
  opacity: 0.6;
}

.header__button {
  display: flex;
  justify-content: center;
  align-items: center;
  min-width: 193px;
  height: 36px;
  color: #ffffff;
  background: var(--color-button-header);
  box-shadow: 0px 1px 4px 1px rgba(36, 36, 36, 0.07);
  border-radius: 120px;
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 18px;
  display: flex;
  align-items: center;
  cursor: pointer;
  border: none;
  transition: opasity 0.3s ease-in-out;
}

.header__button:hover {
  opacity: 0.6;
}
